/* Theme-switch */
body.light-theme {
  background-color: var(--primaryTextColor);
  color: var(--secondaryTextColor);
}

body.dark-theme {
  background-color: #212121;
  color: var(--primaryTextColor);
  .footer,
  .glide__arrow--left,
  .glide__arrow--right {
    box-shadow: 0 0 15px #444444;
    background-color: rgba(21, 21, 21, 0.7);
  }
  .lisence,
  .lisense-common,
  .copyright-logo,
  .text-students,
  .pgn-btn,
  .grid-title,
  .empty-library-title {
    // background-color: transparent;
    color: var(--primaryTextColor);
  }
}

.dark-theme-copyright {
  background-color: transparent;
  color: var(--primaryTextColor);
}

.toolbar {
  padding-top: 16px;
}

.theme__switch {
  display: flex;
  justify-content: flex-end;
  // z-index: 1;
  // position: absolute;

  // top: 15px;
  // right: 15px;

  &-icon {
    color: currentColor;
    height: 20px;
    width: 15px;
    margin-right: 5px;
    margin-left: 5px;
  }

  &-control {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;
    width: 36px;
    height: 20px;
    border-radius: 50em;
    padding: 3px 0;
  }

  &-toggle {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    height: 0;
    width: 0;
    pointer-events: none;
  }

  &-track {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: transparent;
    user-select: none;
    background-color: #333;
    border-radius: inherit;
    z-index: 1;
    cursor: pointer;
  }

  &-marker {
    position: relative;
    background-color: var(--primaryTextColor);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    z-index: 2;
    pointer-events: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
    transform: translateX(3px);
    transition: var(--primary-duration) var(--timing-function);
  }

  &-toggle:checked + &-track {
    background-color: #d3d3d4;
  }

  &-toggle:checked ~ &-marker {
    transform: translateX(13px);
  }

  &-toggle:hover + &-track,
  &-toggle:active + &-track {
    box-shadow: 0 0 0 3px var(--btn-accent-color);
  }
}
